.main-header {
    position: relative;
    width: fit-content;
    margin: 0px auto 80px;
}
.second-title {
    color: #0DCCA5;
}
.second-title::before {
    content: "";
    position: absolute;
    top: 0;
    left: -1.5rem;
    transform: translateX(-50%);
    width: 2rem;
    height: 2rem;
    background-color: white;
    border-radius: 50%;
}
.second-title .number {

    position: absolute;
    top: -1.25rem;
    /* left: -1.5rem; */
    transform: translateX(-50%);
    color: #FBB040;
    font-weight: bolder;
    left: -1.38rem;
}

.main-header::before {
    content: "";
    position: absolute;
    width:  calc(100% + 20px);
    left : -10px;
    height: 30%;
    background-color: #0DCCA5;
    top: 70%;
    border-radius: 17px;
    opacity: 0.41;
}
#about .box {
    background-color: #0000007A !important;
    color: #FFFFFF;
    border-radius: 1rem;
}
#about .vision-title {
    color : #FBB040
}
#about .and-title {
    color : white;
}
#about .vision-title::before,
#about .and-title::before{
    content: "";
    position: absolute;
    height: 200%;
    top: 100%;
    left: 50%;
    border-style: dashed;
    border-color: white;
    border-width: 0px 0px 0px 4px;
}
#about .note .box {
    max-width: 40rem;
}
#about .note h3::after {
    content: '';
    position: absolute;
    height: 100%;
    width: 100%;
    left: 0;
    background-color: #858585;
    opacity: 0.4;
    border-radius: 1rem;
    z-index: -1;
}
#about  .text ul {
    list-style: none;
}
#about  .text ul li {
    padding: 15px 0;
    position: relative;
    font-weight: bolder;
}
#about  .text ul li::before {
content: "";
    position: absolute;
    width: 15px;
    height: 15px;
    background-color: #FBB040;
    margin-left: -26px;
    top: 16px;
    border-radius: 50%;
  }
